<template>
  <!--最近上传组件-->
  <div class="conbody">
    <p>最近上传</p>
    <div class="content">
      <div class="zjsc_list">
        <ul style="overflow: hidden">
          <li v-for="item in recentalbum">
            <img class="img" width="240" :src="item.url"/>
            <p v-html="item.title"></p>
            <p v-html="item.type"></p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  import token from "@/components/token.js"
  //todo 获取最近上传列表
  function getRecentAlbumList(recentalbum) {
    $.ajax({
      url: 'http://houxiaopang.com/api/v1.1/designerdash/index/recent',
      type: "GET",
      headers: {"Authorization": "Token " + token},
      success: function (data) {
        if (data.code != 0) {
          alert("最近上传列表获取失败！");
        } else {
          for (var i = 0, size = data.data.length; i < size; i++) {
            var json = data.data[i];
            var type = json.category;
            if (type == 1) {
              type = "PPT";
            } else if (type == 2) {
              type = "UI";
            } else if (type == 3) {
              type = "文本画册";
            } else if (type == 4) {
              type = "海报展览";
            } else if (type == 5) {
              type = "LOGO";
            } else if (type == 6) {
              type = "企业形象设计（VI）";
            } else if (type == 0) {
              type = "测试品类";
            }
            var obj = {
              url: json.cover_url,
              title: json.title,
              id: json.album_id,
              type: type,
            };
            recentalbum.push(obj);
          }
        }
      },
      error: function () {
        console.log(token);
      }
    });
  }

  export default{
    data(){
      return {
        recentalbum: [], //最近上传列表，对象数组，包含url封面地址，title，品类，id
      }
    },
    created(){
      getRecentAlbumList(this.recentalbum);
    },
  }
</script>
<style>
  .conbody {
    margin: 0 auto;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    position: relative;
  }

  .conbody > p {
    line-height: 40px;
    height: 40px;
    background-color: #f7fafa;
    padding-left: 15px;
  }

  .conbody > .content {
    background: #fff;
    position: absolute;
    top: 40px;
    width: 100%;
  }

  .zjsc_list {
    padding: 15px 0;
    box-sizing: border-box;
    width: 1000px;
    margin: 0 auto;
    height: 100%;
  }

  .zjsc_list > ul {
    overflow: hidden;
    box-sizing: border-box;
    list-style: none;
  }

  .zjsc_list > ul > li {
    width: 240px;
    height: 230px;
    float: left;
    box-sizing: border-box;
    margin-right: 10px;
    margin-bottom: 25px;
  }

  .zjsc_list > ul > li .img {
    width: 100%;
    height: 180px;
  }

  .zjsc_list > ul > li > p:nth-child(2) {
    height: 30px;
    line-height: 30px;
    font-size: 16px;
  }

  .zjsc_list > ul > li > p:nth-child(3) {
    height: 20px;
    line-height: 20px;
    font-size: 14px;
  }
</style>



